.nameEdit input:-webkit-autofill,
.nameEdit input:-webkit-autofill:hover,
.nameEdit input:-webkit-autofill:focus,
.nameEdit input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px var(--main-bg) inset !important;
    -webkit-text-fill-color: var(--white) !important;
}

.nameEdit input {
    outline: none !important;
    border: transparent !important;
    outline-offset: 0 !important;
}

.nameEdit input {
    border: 1px solid transparent !important;
}

.nameEdit .err {
    border-color: var(--red) !important;
}

.groups {
    background-color: var(--second-main);
    height: 100vh;
    width: 100%;
    position: relative;
}

.container {
    display: flex;
    justify-content: space-between;
}

.addFriends {
    width: 50%;
}

.headerContacs {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 50px;
    border-bottom-width: 1px;
    border-style: solid;
    border-color: var(--main-border);
}

.header {
    padding: 0 10px;
}

.groupParams {
    border-right-width: 1px;
    border-color: var(--main-border);
    border-style: solid;
    height: 100vh;
    width: 50%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.friend {
    width: 100%;
    cursor: pointer;
    transition: 0.2s;
}

.containerFriend {
    display: flex;
    align-items: center;
    padding: 10px;
}

.photo {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.photo img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.name {
    margin-left: 15px;
}

.friend:hover {
    background-color: #252632;
}

.addUsers {
    display: none;
    align-items: center;
    padding-bottom: 15px;
}

.blockAddUsersImg {
    width: 27px;
    height: 27px;
}

.blockAddUsersImg img {
    width: 27px;
    height: 27px;
}

.btnAddUsers {
    margin-left: 5px;
    color: var(--desactive-text);
}


.photoGroup {
    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-bottom: 15px;
}

.photoGroup img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.edit,
.nameEdit {
    padding-bottom: 15px;
    position: relative;
}

/* .photoEdit{
    display: flex;
    flex-direction: column;
    align-items: center;
} */
#fileGroup {
    display: none;
}

.iconBtn {
    width: 27px;
    height: 27px;
    position: absolute;
    top: -3px;
}

.choiceFile {
    cursor: pointer;
    color: var(--desactive-text);
    margin-left: 33px;
}

.choiceFile:hover,
.btnAddUsers:hover {
    color: var(--white);
}

.infoSize {
    display: block;
    font-size: 11px;
    color: var(--desactive-text);
    letter-spacing: 0.3px;
}

.btnAdd button {
    background-color: var(--desactive-violet);
    padding: 10px 20px;
    border-radius: 3px;
    color: var(--white);
    line-height: 21px;
    font-size: 15px;
    font-weight: 500;
    transition: 0.2s;
}

.btnAdd button:hover {
    background-color: var(--main-violet);
}

#nameEdit {
    background-color: var(--main-bg);
    padding: 5px;
    color: var(--white);
}

.nameGroup {
    color: var(--desactive-text);
    padding-bottom: 5px;
}

.backBtn {
    display: none;
    position: absolute;
    width: 30px;
    height: 30px;
    left: 20px;
}

.backBtn img {
    width: 30px;
    height: 30px;
}

.nameEdit .errName {
    border-color: var(--red) !important;
}

.emptyUsers{
    font-size: 14px;
    font-weight: normal;
    color:var(--red);
    padding-top: 7px;
}


@media screen and (max-width:500px) {
    .backBtn {
        display: block;
    }

    .groupParams {
        width: 100%;
    }

    .addFriends {
        width: 100%;
        display: none;
    }

    .activeContainer .groupParams {
        display: none;
    }

    .activeContainer .addFriends {
        display: block;
    }

    .addUsers {
        display: flex;
    }
}